import {lineLength} from "./length";
import _ from "lodash";

/**
 *     dl 
 * cosO=(a+b-o)/2ab
 * @param a  
 * @param b  
 * @param o   
 */

export function rotateAngle(a: number[], b: number[], o: number[]) {
    const la = _.round(lineLength([...a, ...o]), 3);
    const lb = _.round(lineLength([...b, ...o]), 3);
    const lo = _.round(lineLength([...a, ...b]), 3);
    /**
     *    acos  [0,1] 
     */
    const arg = _.round((la * la + lb * lb - lo * lo) / (2 * la * lb), 3);
    return _.round(Math.acos(arg > 1 ? 1 : arg) * 180 / Math.PI, 3)
}

/**
 *  (x,y)  0 ~ 360
 */
export function vectorAngle(co: [number, number]): number {
    if (co[0] === 0 && co[1] === 0) return 0;
    return co[1] < 0 ? (360 - rotateAngle(co, [1, 0], [0, 0])) : rotateAngle(co, [1, 0], [0, 0])
}

