  // 
  httpRequest(data: any) {
    this.loadingMessage && this.loadingMessage.close();
    this.loadingMessage = this.$message({
      type: "info",
      duration: 200000,
      message: ", ",
    });
    // @ts-ignore
    window.handleZip
      .parseMapFile({ target: { files: [data.file] } }, {}, true)
      .then((res: any) => {
        this.mergeVisible = false;
        this.mergeUploadFileList = [];
        let {
          width,
          height,
          routes: lines,
          spots,
          originX,
          originY,
          pgmContent: pgm,
        } = res;
        let conf = {
          width,
          height,
          lines,
          spots: this.Topo.handleSpotsFromFile(spots),
          originX,
          originY,
          pgm,
        };
        Object.assign(conf, { SCALE: 20 });
        this.LaserMap.customMerge.renderMergeData(conf);
        this.loadingMessage && this.loadingMessage.close();
      })
      .catch((err: string) => {
        Message.error("" + err);
        this.loadingMessage && this.loadingMessage.close();
      });
  }

