import _ from 'lodash';
import {BORDERWIDTH} from "@/lib/common";

/**
 * 
 * 
 * @param x1 x
 * @param y1 y
 * @param x2 x
 * @param y2 y
 */
const computedSelectRect = (x1 = 0, y1 = 0, x2 = 0, y2 = 0) => {
    let x_min = _.min([x1, x2]) || 0;
    let y_min = _.min([y1, y2]) || 0;
    return {
        x: x_min - BORDERWIDTH | 0,
        y: y_min - BORDERWIDTH | 0,
        width: Math.abs(x2 - x1) + 2 * BORDERWIDTH || 0,
        height: Math.abs(y2 - y1) + 2 * BORDERWIDTH || 0
    }
};

export default computedSelectRect
