import Vue from 'vue'

// 
export const passwordValidation = async (title) => {
    let result;
    if (window.globalConfig.needPassword) {
        await Vue.prototype.$prompt("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            inputValidator: (msg) => {
                // @ts-ignore
                return msg === String(window.globalConfig.password)
            },
            inputErrorMessage: ", "
        })
            .then(() => { result = true })
            .catch(() => { result = false });
    } else {
        if (!title) return true; // 
        await Vue.prototype.$confirm(`${title}?`, "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
        }).then(() => { result = true }).catch(() => { result = false });
    }
    return result
}

// rgb
export const getRGB = (color) => {
    return "rgb(" +
        Math.floor(color[0]) +
        "," +
        Math.floor(color[1]) +
        "," +
        Math.floor(color[2]) +
        ")"
}