import Topology from "@/lib/core/topo";
import _ from "lodash";
import { BORDERWIDTH, CONFIDENCECOLORS, LASERWIDTH, PRECISION, AREACOLOR } from "@/lib/common";
import computedSelectRect from "@/lib/utils/both-way-rect-select";
import Command from "@/lib/core/command";
import copy from "@/lib/utils/copy";
import delayFunc from "@/lib/utils/delay-func";
import MergeMap from "@/lib/core/custom/merge-template";
import { vectorAngle } from "@/lib/utils/rotate-angle";
import { transformRotate } from "@/lib/utils/transform-rotate";
import inRect from "@/lib/utils/in-rect";
import { copySpotProps } from "@/lib/core/custom/edit-spot";
import { copyRouteProps } from "@/lib/core/custom/edit-route";
import { absoluteToRelativeCoordinate, relativeToAbsoluteCoordinate, relativeToLaserCoordinate } from "@/lib/core/custom/utils"
import lineIntersect from "@/lib/utils/line-intersect";
import thumbnailCanvas from "@/lib/utils/thumbnail";


class LaserMap {

    static instance: LaserMap;


    /**
     * DOM  context
     */
    public canvas: HTMLCanvasElement | null = null;
    private readonly canvasCtx: CanvasRenderingContext2D | null = null;

    /**
     * 
     */
    private readonly laserData: HTMLImageElement | null = null;
    private readonly laserCanvas: HTMLCanvasElement | null = null;
    private laserCtx: CanvasRenderingContext2D | null = null;

    /**
     *  
     *    canvas 
     */
    private readonly boundaryCanvas: HTMLCanvasElement | null = null;
    private boundaryCtx: CanvasRenderingContext2D | null = null;

    /**
     * 
     */
    private readonly workspaceCanvas: HTMLCanvasElement | null = null;
    private workspaceCtx: CanvasRenderingContext2D | null = null;

    /**
     * 
     * 
     */
    public customMerge: MergeMap = new MergeMap();


    /**
     * 
     * merging  merge 
     */
    mergeCanvas: HTMLCanvasElement | null = null;
    mergeCtx: CanvasRenderingContext2D | null = null;
    //  
    private mergeHandleArea: number[] = [];

    /**
     * 
     * 1
     * 2
     * 3
     * (mx,my)
     * enable 
     * handleArea  [x1,y1,x2,y2]
     */
    private readonly handlerCanvas: HTMLCanvasElement | null = null;
    private handlerCtx: CanvasRenderingContext2D | null = null;
    mx: number = 0;
    my: number = 0;
    private enable: boolean = false;
    private handleArea: number[] = [];


    /**
     * 
     * @param url URL
     */
    constructor() {
        /**
         *  canvas
         */
        this.canvas = this.createCanvas();
        this.canvas.style.position = 'absolute';
        this.canvas.style.top = 0 + 'px';
        this.canvas.style.bottom = 0 + 'px';
        this.canvas.style.left = 0 + 'px';
        this.canvas.style.right = 0 + 'px';
        this.canvas.style.zIndex = '-1';
        // @ts-ignore
        Topology.instance.ele.insertAdjacentElement('afterbegin', this.canvas);
        this.canvasCtx = this.canvas.getContext('2d');
        // @ts-ignore
        this.canvasCtx.imageSmoothingEnabled = false;
        /**
         * 
         */
        // @ts-ignore
        this.canvasCtx.font = '24px serif';
        // @ts-ignore
        this.canvasCtx.fillStyle = 'rgba(255,255,255,0.55)';
        // @ts-ignore
        this.canvasCtx.textAlign = 'center';
        // @ts-ignore
        this.canvasCtx.fillText('', this.canvas.width / 2 + 25, this.canvas.height / 2 + 25);

        /**
         * 
         */
        this.boundaryCanvas = this.createCanvas();
        this.boundaryCtx = this.boundaryCanvas.getContext('2d');

        /**
         * 
         */
        this.laserCanvas = this.createCanvas();

        /**
         * 
         */
        this.workspaceCanvas = this.createCanvas();
        this.workspaceCtx = this.workspaceCanvas.getContext('2d');


        /**
         * 
         * 
         */
        this.handlerCanvas = this.createCanvas();


        /**
         * 
         * 
         */
        this.mergeCanvas = this.createCanvas();
        this.mergeCtx = this.mergeCanvas.getContext('2d');


        /**
         * 
         */
        this.mouseHandler();

        LaserMap.instance = this;
    }

    /**
     * 
     */
    renderMapCloud(imgData: ImageData) {
        let { width, height } = Topology.instance;
        // @ts-ignore
        this.laserCanvas.width = width;
        // @ts-ignore
        this.laserCanvas.height = height;
        // @ts-ignore
        this.laserCtx = this.laserCanvas.getContext('2d');


        // @ts-ignore
        this.workspaceCanvas.width = width;
        // @ts-ignore
        this.workspaceCanvas.height = height;
        // @ts-ignore
        this.workspaceCtx = this.workspaceCanvas.getContext('2d');
        // /**
        //  * 
        //  */
        // // @ts-ignore
        // this.workspaceCtx.fillStyle = 'rgba(0,0,0,0.6)';
        // // @ts-ignore
        // this.workspaceCtx.fillRect(0, 0, this.workspaceCanvas.width, this.workspaceCanvas.height);
        // // @ts-ignore
        // this.workspaceCtx.fill();

        // @ts-ignore
        this.handlerCanvas.width = width;
        // @ts-ignore
        this.handlerCanvas.height = height;
        // @ts-ignore
        this.handlerCtx = this.handlerCanvas.getContext('2d');
        // @ts-ignore
        this.handlerCtx.imageSmoothingEnabled = false;

        // @ts-ignore
        this.laserCtx.putImageData(this.imageDataCopy(imgData), 0, 0);

        this.render();
    }

    /**
     * render laser map
     * 
     */
    render() {
        // @ts-ignore
        this.canvasCtx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        let { gx, gy, scale } = Topology.instance;
        /**
         * 
         */
        // @ts-ignore
        let { width: w1, height: h1 } = this.workspaceCanvas;
        // @ts-ignore
        this.canvasCtx.drawImage(this.workspaceCanvas, gx, gy, w1 * scale, h1 * scale);

        /**
         * 
         */
        // @ts-ignore
        let { width: w2, height: h2 } = this.laserCanvas;
        // @ts-ignore
        this.canvasCtx.drawImage(this.laserCanvas, gx, gy, w2 * scale, h2 * scale);

        /**
         * 
         */
        if (this.customMerge.visible) {
            this.renderMergeLayer();
            // 
            // @ts-ignore
            // this.canvasCtx.drawImage(this.boundaryCanvas, 0, 0);
            // @ts-ignore
            // let {width: bw, height: bh} = this.boundaryCanvas;
            // @ts-ignore
            // this.canvasCtx.drawImage(this.boundaryCanvas, gx, gy, bw * scale, bh * scale);
        }

        /**
         * 
         */
        // @ts-ignore
        let { width: w3, height: h3 } = this.handlerCanvas;
        // @ts-ignore
        this.canvasCtx.drawImage(this.handlerCanvas, gx, gy, w3 * scale, h3 * scale);

    }

    /**
     *  canvas factory
     */
    createCanvas() {
        let canvas = document.createElement('canvas');
        // @ts-ignore
        const { width, height } = Topology.instance.ele.getBoundingClientRect();
        canvas.width = width;
        canvas.height = height;
        return canvas
    }


    /**
     *   
     * 
     * 
     * @param x1
     * @param y1
     * @param x2
     * @param y2
     */
    renderRectArea(x1: number, y1: number, x2: number, y2: number) {
        let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
        let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
        let { x, y, width, height } = computedSelectRect(rx1, ry1, rx2, ry2);
        // @ts-ignore
        this.handlerCtx.clearRect(0, 0, this.handlerCanvas.width, this.handlerCanvas.height);
        // @ts-ignore
        this.handlerCtx.fillStyle = 'rgba(255,255,0,0.3)';
        // @ts-ignore
        this.handlerCtx.fillRect(x, y, _.floor(width), _.floor(height));
        // @ts-ignore
        this.handlerCtx.fill();

        this.render();
    }

    /**
     * 
     * @param x1
     * @param y1
     * @param x2
     * @param y2
     */
    renderMergeRectArea(x1: number, y1: number, x2: number, y2: number) {
        // laserCanvas 
        let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
        let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
        //   
        // TODO
        let { x, y, width, height } = computedSelectRect(rx1, ry1, rx2, ry2);
        // console.log(x, y)
        // if (x < 0 || y < 0) {
        //
        // }
        //  
        // @ts-ignore
        let { width: w, height: h } = this.mergeCanvas;
        let ag = this.customMerge.angle * Math.PI / 180;
        this.mergeHandleArea = [rx1, ry1, rx2, ry2];

        // 
        // @ts-ignore
        this.mergeCtx.clearRect(0, 0, w, h);
        // 
        // @ts-ignore
        this.mergeCtx?.drawImage(this.customMerge.layerCvs, 0, 0,);
        /**
         *  
         * 
         */
        // @ts-ignore
        this.mergeCtx.fillStyle = 'rgba(255,255,255,0.1)';
        this.mergeCtx?.fillRect(0, 0, w, h);
        this.mergeCtx?.fill();

        // 
        // @ts-ignore
        this.mergeCtx?.translate(w / 2, h / 2);
        this.mergeCtx?.rotate(-ag);
        // @ts-ignore
        this.mergeCtx.fillStyle = '#ffffff33';
        this.mergeCtx?.fillRect(x - this.customMerge.mergeX - w / 2, y - this.customMerge.mergeY - h / 2, width, height);
        this.mergeCtx?.fill();
        this.mergeCtx?.rotate(ag);
        // @ts-ignore
        this.mergeCtx?.translate(-w / 2, -h / 2);
        this.render();
    }

    /**
     *  Topo  
     *  
     */
    computeMergeArea() {
        if (this.mergeHandleArea.length !== 4) {
            return
        }
        let { x, y, width, height } = computedSelectRect(...this.mergeHandleArea);
        if (!width || !height) return
        //  cvs 
        // TODO
        //  laserCvs 
        // 
        let borderSize = [_.min([x, 0]),
        _.min([y, 0]),
        // @ts-ignore
        _.max([width + x, this.laserCanvas.width]),
        // @ts-ignore
        _.max([height + y, this.laserCanvas.height])];

        let ag = this.customMerge.angle * Math.PI / 180;
        // cvs 
        let cvs_px = x - this.customMerge.mergeX;
        let cvs_py = y - this.customMerge.mergeY;
        let imgData, laserDataCopy;
        let pgmWidth = this.customMerge.pgmContent.width
        let pgmHeight = this.customMerge.pgmContent.height
        if (ag) {
            let diagonal = _.round(Math.sqrt(pgmWidth * pgmWidth + pgmHeight * pgmHeight));
            this.customMerge.squareCvs.width = this.customMerge.squareCvs.height = diagonal;
            this.customMerge.squareCtx?.clearRect(0, 0, diagonal, diagonal);
            this.customMerge.squareCtx?.translate(diagonal / 2, diagonal / 2);
            this.customMerge.squareCtx?.rotate(ag);
            // @ts-ignore 
            this.customMerge.squareCtx?.drawImage(this.customMerge.cvs, this.customMerge.dx - diagonal / 2, this.customMerge.dy - diagonal / 2);
            this.customMerge.squareCtx?.rotate(-ag);
            this.customMerge.squareCtx?.translate(-diagonal / 2, -diagonal / 2);
            // @ts-ignore
            imgData = window.handleZip.correctImageData(this.customMerge.squareCtx?.getImageData(cvs_px + this.customMerge.dx, cvs_py + this.customMerge.dy, _.ceil(width), _.ceil(height)));
        } else {
            this.customMerge.squareCvs.width = pgmWidth + Math.abs(this.customMerge.mergeX)
            this.customMerge.squareCvs.height = pgmHeight + Math.abs(this.customMerge.mergeY)
            this.customMerge.squareCtx?.clearRect(0, 0, this.customMerge.pgmContent.width, this.customMerge.pgmContent.height);
            // @ts-ignore 
            this.customMerge.squareCtx?.putImageData(this.customMerge.pgmContent, this.customMerge.mergeX, this.customMerge.mergeY);
            // @ts-ignore
            imgData = window.handleZip.correctImageData(this.customMerge.squareCtx?.getImageData(x, y, width, height));
        }
        // 
        // @ts-ignore
        laserDataCopy = this.laserCtx.getImageData(0, 0, this.laserCanvas.width, this.laserCanvas.height);
        // @ts-ignore
        this.laserCanvas.width = borderSize[2] - borderSize[0];
        // @ts-ignore
        this.laserCanvas.height = borderSize[3] - borderSize[1];
        // @ts-ignore
        this.laserCtx = this.laserCanvas.getContext('2d');
        // @ts-ignore
        this.laserCtx.putImageData(laserDataCopy, x < 0 ? -x : 0, y < 0 ? -y : 0);

        // 
        // @ts-ignore
        [this.handlerCanvas.width, this.handlerCanvas.height] = [this.laserCanvas.width, this.laserCanvas.height];
        // @ts-ignore
        this.handlerCtx = this.handlerCanvas.getContext('2d');
        // @ts-ignore
        // this.handlerCtx.imageSmoothingEnabled = false;

        // @ts-ignore
        this.laserCtx?.putImageData(imgData, x < 0 ? 0 : x, y < 0 ? 0 : y);

        //    &   [refId]  refId 
        let selectedSpots = new Map();
        this.customMerge.tpData.spots.forEach((spot: any) => {
            // @ts-ignore
            let p = transformRotate([spot['cvsx'], spot['cvsy']], [this.customMerge.layerCvs.width / 2, this.customMerge.layerCvs.height / 2], ag);
            // 1cvs  canvasCtx 
            let _x = p[0] + this.customMerge.mergeX;
            let _y = p[1] + this.customMerge.mergeY;
            // @ts-ignore
            if (inRect([_x, _y], [x, y, x + width, y + height])) {
                // 2 
                let { ax, ay } = relativeToAbsoluteCoordinate(_x, _y);
                // 
                spot.dir *= 180 / Math.PI;
                // @ts-ignore true
                let newSpot = Topology.instance.addSpot(ax, ay, copySpotProps(spot), false, true);
                newSpot && selectedSpots.set(spot.refId, newSpot.refId);
            }
        });
        //  route
        let oldRefIdCollection = Array.from(selectedSpots.keys());
        this.customMerge.tpData.lines.forEach((route: any) => {
            if (oldRefIdCollection.includes(route['startSpotRefId']) && oldRefIdCollection.includes(route['endSpotRefId'])) {
                Topology.instance.newRoute.start = selectedSpots.get(route['startSpotRefId']) || 0;
                Topology.instance.newRoute.end = selectedSpots.get(route['endSpotRefId']) || 0;
                // @ts-ignore
                Topology.instance.addRoute(copyRouteProps(route), false);
                Topology.instance.newRoute.start = Topology.instance.newRoute.end = 0;
            }
        });

        //   
        // TODO
        // @ts-ignore
        Topology.instance.width = this.laserCanvas.width;
        // @ts-ignore
        let diffY = this.laserCanvas.height - Topology.instance.height;
        // @ts-ignore
        Topology.instance.height = this.laserCanvas.height;
        // 
        Topology.instance.ox += _.min([x, 0])
        Topology.instance.oy -= _.min([y, 0]) + diffY
        // Topology.instance.spotsMapper.forEach(spot => {
        //     // @ts-ignore
        //     spot.x -= _.min([x, 0]);
        //     // @ts-ignore
        //     spot.y -= _.min([y, 0]) + diffY;
        // });
        // @ts-ignore
        Topology.instance.gx += _.min([x, 0]) * Topology.instance.scale;
        // @ts-ignore
        Topology.instance.gy += _.min([y, 0]) * Topology.instance.scale;
        Topology.instance.reRender();
        Topology.instance.rangeDraw();
        thumbnailCanvas.instance.render();// 

        // let [x1, y1, x2, y2] = this.mergeHandleArea;
        //  TOPO 
        // this.customMerge.mergeData.push({
        //     ag: this.customMerge.angle,
        //     data: {x: x1, y: y1, width: x2 - x1, height: y2 - y1},
        //     tp: []
        // });
        //
        this.mergeHandleArea = [];
        //   
        // @ts-ignore
        this.customMerge.mergeX -= _.min([x, 0]);
        // @ts-ignore
        this.customMerge.mergeY -= _.min([y, 0]);

        // canvas
        let { width: w, height: h } = this.mergeCanvas
        this.customMerge.layerCtx?.translate(w / 2, h / 2);
        this.customMerge.layerCtx?.rotate(-ag);
        // @ts-ignore
        this.customMerge.layerCtx.fillStyle = AREACOLOR;
        this.customMerge.layerCtx?.fillRect(x - this.customMerge.mergeX - w / 2, y - this.customMerge.mergeY - h / 2, width, height);
        this.customMerge.layerCtx?.fill();
        this.customMerge.layerCtx?.rotate(ag);
        // @ts-ignore
        this.customMerge.layerCtx?.translate(-w / 2, -h / 2);
        this.renderMergeRectArea(0, 0, 0, 0);
    }

    /**
     *   
     * 
     * @param x1
     * @param y1
     * @param x2
     * @param y2
     */
    private renderLine(x1: number, y1: number, x2: number, y2: number, level: number = 0) {
        let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
        let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
        // @ts-ignore
        this.handlerCtx.clearRect(0, 0, this.handlerCanvas.width, this.handlerCanvas.height);
        // @ts-ignore
        this.handlerCtx.beginPath();
        // @ts-ignore
        this.handlerCtx.strokeStyle = `rgb(${CONFIDENCECOLORS[level].join(',')})`;
        // @ts-ignore
        this.handlerCtx.lineWidth = LASERWIDTH;
        [rx1, ry1, rx2, ry2] = [_.floor(rx1), _.floor(ry1), _.floor(rx2), _.floor(ry2)];
        // @ts-ignore
        this.handlerCtx.moveTo(rx1, ry1);
        // @ts-ignore
        this.handlerCtx.lineTo(rx2, ry2);
        // @ts-ignore
        this.handlerCtx.stroke();
        // @ts-ignore
        this.handlerCtx.closePath();

        this.render();
    }

    /**
     *  imgData 
     *  imgData 
     */
    private static paintPath(imgData: ImageData, color: number = 0): ImageData {
        for (let i = 0; i < imgData.data.length; i += 4) {
            if (imgData.data[i] || imgData.data[i + 1] || imgData.data[i + 2]) {
                imgData.data[i] = CONFIDENCECOLORS[color][0];
                imgData.data[i + 1] = CONFIDENCECOLORS[color][1];
                imgData.data[i + 2] = CONFIDENCECOLORS[color][2];
                imgData.data[i + 3] = 255;
            }
        }
        return imgData;
    }

    /**
     *  &  
     */
    clearHandlerArea() {
        this.handleArea = [];
        // @ts-ignore
        this.handlerCtx.clearRect(0, 0, this.handlerCanvas.width, this.handlerCanvas.height);
        this.render();
    }


    /**
     *   &  
     */
    private addLaserData(level: number = 0) {
        if (this.handleArea.length === 4) {
            const [x1, y1, x2, y2] = this.handleArea;
            let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
            let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
            let { x, y, width, height } = computedSelectRect(rx1, ry1, rx2, ry2);
            [x, y, width, height] = [_.ceil(x - 1), _.ceil(y - 1), _.ceil(width + 2), _.ceil(height + 2)];
            if (width && height) {
                // @ts-ignore
                const oldImgData = this.imageDataCopy(this.laserCtx.getImageData(x, y, width, height));
                // @ts-ignore
                this.laserCtx.beginPath();
                // @ts-ignore
                this.laserCtx.lineWidth = LASERWIDTH;
                // @ts-ignore
                this.laserCtx.strokeStyle = `rgb(${CONFIDENCECOLORS[level].join(',')})`;
                [rx1, ry1, rx2, ry2] = [_.floor(rx1), _.floor(ry1), _.floor(rx2), _.floor(ry2)];
                // @ts-ignore
                this.laserCtx.moveTo(rx1, ry1);
                // @ts-ignore
                this.laserCtx.lineTo(rx2, ry2);
                // @ts-ignore
                this.laserCtx.stroke();
                // @ts-ignore
                this.laserCtx.closePath();


                // @ts-ignore
                let data = this.laserCtx.getImageData(x, y, width, height);
                // @ts-ignore
                data = window.handleZip.correctImageData(data, level)
                //@ts-ignore
                this.laserCtx.putImageData(data, x, y);

                // @ts-ignore
                const newImgData = this.imageDataCopy(data);
                this.clearHandlerArea();
                /**
                 * Command
                 */
                Command.execute({
                    directive: 32,
                    type: 'UPDATE',
                    target: [{ x, y, data: newImgData }, { x, y, data: oldImgData }]
                });
            }
        }
        this.handleArea = [];
    }

    /**
     * DOM 
     */
    private mouseHandler() {
        /**
         * 
         * firefox & chrome
         */
        // @ts-ignore
        Topology.instance.ele.addEventListener('wheel', this.mouseWheelHandler.bind(this), false);

        // @ts-ignore
        Topology.instance.ele.addEventListener('mousemove', (e: Event | any) => {
            if (this.customMerge.visible) {
                if (this.enable) {
                    if (this.customMerge.rotatable) {
                        // console.log(' - ');
                        this.renderMergeRectArea(this.mx, this.my, e.pageX, e.pageY);
                    } else {
                        // console.log(' - ');
                        this.customMerge.mergeX += _.round((e.pageX - this.mx) / Topology.instance.scale, PRECISION);
                        this.customMerge.mergeY += _.round((e.pageY - this.my) / Topology.instance.scale, PRECISION);
                        this.render();
                        this.mx = e.pageX;
                        this.my = e.pageY;
                    }
                }
            }
            if (Topology.instance.movable) {
                this.render();
            }
            if (this.enable) {
                // @ts-ignore
                if (Topology.instance.state === 5 || Topology.instance.state[0] === 7 || Topology.instance.state === 11) {
                    this.handleArea = [this.mx, this.my, e.pageX, e.pageY];
                    // @ts-ignore
                    this.renderRectArea(...this.handleArea);
                }
                // @ts-ignore
                if (Topology.instance.state[0] === 6) {
                    this.handleArea = [this.mx, this.my, e.pageX, e.pageY];
                    // @ts-ignore
                    this.renderLine(...this.handleArea, Topology.instance.state[1]);
                }
            }
        });

        // @ts-ignore
        Topology.instance.ele.addEventListener('mousedown', (e: Event | any) => {
            if (e.button === 0) {
                // if (Topology.instance.state === 5) {
                // }
                this.mx = e.pageX;
                this.my = e.pageY;
                this.enable = true;
                if (this.customMerge.visible) {
                    this.customMerge.mousedown(e);
                }
                // @ts-ignore
                if (Topology.instance.state[0] === 6) {
                    // 
                    let { rx, ry } = absoluteToRelativeCoordinate(e.pageX, e.pageY);
                    [rx, ry] = [_.floor(rx), _.floor(ry)]
                    let { ax, ay } = relativeToAbsoluteCoordinate(rx + 1, ry + 1)
                    this.handleArea = [e.pageX, e.pageY, ax, ay]
                    // @ts-ignore
                    this.renderLine(...this.handleArea, Topology.instance.state[1]);
                }
            }
        });

        // @ts-ignore
        Topology.instance.ele.addEventListener('mouseup', (e: Event | any) => {
            if (e.button === 0) {
                if (Topology.instance.state === 5) {
                    this.deleteLaserData();
                }
                if (Topology.instance.state === 11) {
                    this.deletePlate();
                }
                // @ts-ignore
                if (Topology.instance.state[0] === 7) {
                    // @ts-ignore
                    this.setLaserDataConfidence(Topology.instance.state[1]);
                }

                // @ts-ignore
                if (Topology.instance.state[0] === 6) {
                    // @ts-ignore
                    this.addLaserData(Topology.instance.state[1]);
                }

                //   
                if (this.customMerge.visible) {
                    if (this.enable) {
                        if (this.customMerge.rotatable) {
                            this.computeMergeArea();
                        }
                    }
                }

                // 
                this.enable = false;
            }

        });

        /**
         * 
         */
        window.addEventListener('resize', this.laserMapResize);
    }
    public laserMapResize() {
        return
    }

    /**
     * 
     * @param e
     */
    mouseWheelHandler(e: Event | any) {
        // 
        if (!Topology.instance.width || !Topology.instance.height) {
            e.stopPropagation();
            e.preventDefault();
            return
        }
        this.render();
    }

    /**
     * 
     * @param level
     */
    private setLaserDataConfidence(level: number) {
        if (this.handleArea.length === 4) {
            const [x1, y1, x2, y2] = this.handleArea;
            let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
            let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
            const { x, y, width, height } = computedSelectRect(rx1, ry1, rx2, ry2);
            if (width && height) {
                // @ts-ignore
                let imgData = this.laserCtx.getImageData(x, y, width, height);
                const oldImgData = this.imageDataCopy(imgData);
                imgData = LaserMap.paintPath(imgData, level);
                const newImgData = this.imageDataCopy(imgData);
                // @ts-ignore
                this.laserCtx.putImageData(imgData, x, y);
                this.clearHandlerArea();
                /**
                 * Command
                 */
                Command.execute({
                    directive: 33,
                    type: 'UPDATE',
                    target: [{ x, y, data: newImgData }, { x, y, data: oldImgData }]
                });
            }
        }
    }

    /**
     * 
     */
    private deleteLaserData() {
        if (this.handleArea.length === 4) {
            const [x1, y1, x2, y2] = this.handleArea;
            let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
            let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
            const { x, y, width, height } = computedSelectRect(rx1, ry1, rx2, ry2);
            if (width && height) {
                // @ts-ignore
                const imgData = this.laserCtx.getImageData(x, y, width, height);
                /**
                 * 
                 */
                const oldImgData = this.imageDataCopy(imgData);

                for (let i = 0; i < imgData.data.length; i += 4) {
                    imgData.data[i + 3] = 0;
                }

                /**
                 * 
                 */
                const newImgData = this.imageDataCopy(imgData);
                this.drawLaserData(x, y, imgData);
                this.clearHandlerArea();
                // this.render();
                /**
                 * Command
                 */
                Command.execute({
                    directive: 31,
                    type: 'DELETE',
                    target: [{ x, y, data: newImgData }, { x, y, data: oldImgData }]
                });
            }
        }
    }

    /**
     * 
     */
    private deletePlate() {
        if (this.handleArea.length === 4) {
            const [x1, y1, x2, y2] = this.handleArea;
            let { rx: rx1, ry: ry1 } = absoluteToRelativeCoordinate(x1, y1);
            let { rx: rx2, ry: ry2 } = absoluteToRelativeCoordinate(x2, y2);
            const { width, height } = computedSelectRect(rx1, ry1, rx2, ry2);
            if (width && height) {
                let { lx: lx1, ly: ly1 } = relativeToLaserCoordinate(rx1, ry1);
                let { lx: lx2, ly: ly2 } = relativeToLaserCoordinate(rx2, ry2);
                let deletePlates: Array<any> = []
                Topology.instance.plateMapper.forEach((plate: any) => {
                    plate.forEach((item: { x: number, y: number }, i: number) => {
                        let { x, y } = item
                        if (x === undefined || y === undefined) return
                        if (inRect([x, y], [lx1, ly1, lx2, ly2])) {
                            Topology.instance.removePlate({ id: plate.id, i })
                            deletePlates.push({ item, i, id: plate.id })
                        }
                    });
                })
                this.clearHandlerArea();
                Command.execute({
                    directive: 661,
                    type: 'DELETE',
                    target: deletePlates
                });
            }
        }
    }

    imageDataCopy(imgData: ImageData) {
        return new ImageData(imgData.data.slice(0, imgData.data.length), imgData.width, imgData.height);
    }

    /**
     * 
     * 
     * 
     * 
     * @param x
     * @param y
     * @param data
     */
    drawLaserData(x: number, y: number, data: any) {
        // @ts-ignore
        this.laserCtx.putImageData(data, x, y);
        this.render();
    }

    /**
     * 
     * @param data
     */
    renderMergeData(data?: any) {
        if (data === undefined) {
            //  
            // @ts-ignore
            this.mergeCtx?.clearRect(0, 0, this.mergeCanvas.width, this.mergeCanvas.height);
            this.render();
            return
        }
        // 
        this.customMerge.visible = true;
        // @ts-ignore
        let { width, height } = this.customMerge.layerCvs;
        // @ts-ignore
        [this.mergeCanvas.width, this.mergeCanvas.height] = [width, height];
        // @ts-ignore
        this.mergeCtx = this.mergeCanvas?.getContext('2d');


        // /**
        //  * rotate 
        //  */
        // // @ts-ignore
        // this.mergeCtx.fillStyle = 'rgba(0, 205, 102,1)';
        // // @ts-ignore
        // this.mergeCtx.fillRect(this.mergeCanvas.width - 20, this.mergeCanvas.height / 2 - 10, 20, 20);
        // // @ts-ignore
        // this.mergeCtx.fill();

        /**
         * 
         * 
         */
        this.mergeCtx?.save();
        // @ts-ignore
        this.mergeCtx.fillStyle = 'rgba(255,255,255,0.1)';
        // @ts-ignore
        this.mergeCtx.fillRect(0, 0, this.mergeCanvas.width, this.mergeCanvas.height);
        this.mergeCtx?.fill();


        // @ts-ignore
        this.mergeCtx.drawImage(this.customMerge.layerCvs, 0, 0, width, height);
        this.render();
    }

    /**
     * merge 
     */
    private renderMergeLayer() {
        //  mergeCanvas   angle 
        let { gx, gy, scale } = Topology.instance;
        // @ts-ignore
        let { width, height } = this.mergeCanvas;
        let ox = gx + this.customMerge.mergeX * scale;
        let oy = gy + this.customMerge.mergeY * scale;
        let radian = this.customMerge.angle * Math.PI / 180;
        // mergeCanvas  (cx,cy)
        let cx = width * scale / 2, cy = height * scale / 2;
        this.canvasCtx?.translate(ox + cx, oy + cy);
        this.canvasCtx?.rotate(radian);
        // @ts-ignore
        this.canvasCtx.drawImage(this.mergeCanvas, -cx, -cy, width * scale, height * scale);
        this.canvasCtx?.rotate(-radian);
        this.canvasCtx?.translate(-(ox + cx), -(oy + cy));
    }

    /**
     * 
     */


    /**
    * 
    */
    getLaserData() {
        // @ts-ignore
        return { pgmContent: this.laserCtx.getImageData(0, 0, this.laserCanvas.width, this.laserCanvas.height), pgmWidth: this.laserCanvas.width, pgmHeight: this.laserCanvas.height }
    }
}

export default LaserMap
