// 3 
const rest = new Image();
rest.src = srcPublic + 'spot_3.png';
// 4 
const dock = new Image();
dock.src = srcPublic + 'spot_4.png';
// 5 
const roller = new Image();
roller.src = srcPublic + 'spot_5.png';

// 6 
let stationList = (state?: number) => {
  let station = new Image();
  switch (state) {
    case 1:
      station.src = srcPublic + 'point-station-reserve.png'
      break;
    case 2:
      station.src = srcPublic + 'point-station-using.png'
      break;
    default:
      station.src = srcPublic + 'point-station-normal.png'
      break;
  }
  return station
}

