/**
 * Event Loop
 *  
 * 
 * @param cb 
 * @param delay   ms
 */

// 
let flag = false;
// 
let timer: any | 0 = 0;
const delayFunc = (cb: Function, delay: number = 300) => {
    if (!flag) {
        timer = setTimeout(() => {
            cb();
            flag = false;
            clearTimeout(timer);
        }, delay);
        flag = true;
    }
};
export default delayFunc
