
import { MessageBox } from "element-ui";
import md5 from "blueimp-md5";

const reader = new FileReader();
let beginData = {}  //

// 
export const saveBeginData = (key, oldValue) => {
    if (!beginData) beginData = {}
    if (typeof oldValue === 'object') {
        // blob
        if (oldValue.size && typeof oldValue.type === 'string' && oldValue.type.indexOf("application/") === 0) {
            reader.readAsText(oldValue); //
            reader.onload = (e) => {
                if (e.target) {
                    // @ts-ignore
                    beginData[key] = md5(e.target.result)
                }
            };
        } else {
            beginData[key] = JSON.stringify(oldValue)
        }
    } else {
        beginData[key] = oldValue
    }
}

// 
export const handleNewData = async (key, newValue, callback) => {
    if (!beginData) beginData = {}
    let fun = () => {
        if (beginData[key] === newValue) {
            callback()
        } else {
            MessageBox.confirm("?", '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'error',
            }).then(async () => {
                callback()
            }).catch(() => { });
        }
    }
    if (typeof newValue === 'object') {
        // blob
        if (newValue.size && typeof newValue.type === 'string' && newValue.type.indexOf("application/") === 0) {
            reader.readAsText(newValue); //
            reader.onload = (e) => {
                if (e.target) {
                    // @ts-ignore
                    newValue = md5(e.target.result)
                    fun()
                }
            };
        } else {
            newValue = JSON.stringify(newValue)
            fun()
        }
    } else {
        fun()
    }
}

// 
export const destroyBeginData = () => {
    beginData = null
}